import React, { useState, useEffect } from 'react';
import styled from "styled-components";
import { useWeb3 } from "../api/contracts";
import PublicObj from "../utils/publicJs";
import services from "../api/services";


const Bg = styled.div`
    max-width:1037px;
    width: 100%;
    height: 100vh;
    position: fixed;
    top:0;
        display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
  z-index: 9999;
`;
const BgLayer = styled.div`
    background: rgba(0,4,51,0.85);
   color: #ffffff;
  max-width: 100%;
  overflow-y: auto;
  margin: 20px auto 0;
  padding: 62px 54px;
`;
const Li = styled.div`
  margin-bottom: 16px;
`
const Titles = styled.span`
    font-size: 14px;
    font-family: SF-Pro-Display;
    font-weight: bolder;
`;
const Titles2 = styled.span`
    font-size: 14px;
    font-family: d-din;
    font-weight: bolder;
`;
const Names = styled.span`
    width: 65px;
    display: inline-block;
    text-align: right;
    opacity: 0.8;
    font-size: 12px;
    font-weight: 400;
    margin-right: 8px;
`;

const Btn = styled.button`
      background: linear-gradient(180.25deg, rgba(36,114,244,1) 0%, rgba(50,65,244,1) 100%);
      border: 0;
      color: #ffffff;
      margin-left: 0;
      width: 48%;
      cursor: pointer;
`;
const BtnClose = styled.button`
      background: transparent;
      border: 1px solid #fff;
      color: #ffffff;
      margin-left: 0;
      width: 48%;
       cursor: pointer;
`;
const LastRow = styled.div`
    display: flex;
    justify-content: space-between;
    align-content: center;
    margin-top: 28px;
`;
const Inpt = styled.input`
  background: transparent;
  border: 1px solid #fff;
  width: 152px;
  height: 24px;
  color: #ffffff;
  font-family: d-din-bold;
 
  padding: 0 8px;
  &:focus{
    outline: none;
  }
`;
const Max = styled.span`
    font-size: 12px;
    font-weight: 400;
    margin-left: 6px;
    text-decoration: underline;
`

export default function StakeModal(props) {
    const { state, dispatch } = useWeb3();
    const { mybalance, Account } = state;
    const { closeBox, detailId, name } = props;

    const [amount, setAmount] = useState('');
    const [staking, setStaking] = useState(false);
    const { maincontract, web3Instance, erc20contract } = state;

    useEffect(() => {

        if (!staking) return;

        const stake = async () => {
            dispatch({
                type: 'SET_LOADING', payload: {
                    tips: "Stake...",
                }
            });
            services.stake(maincontract, web3Instance, detailId, amount, Account).then((data) => {
                dispatch({ type: 'SET_LOADING', payload: null });
                dispatch({ type: 'BALANCE_INIT' });
                closeBox()
            }).catch(err => {
                dispatch({ type: 'SET_LOADING', payload: null });
                dispatch({ type: 'BALANCE_INIT' });
            })
        }
        stake()
    }, [staking]);

    const tomax = () => {
        setAmount(mybalance)
    }

    const handleChange = (e) => {
        setAmount(e.target.value)
    }

    const handleStake = async () => {
        dispatch({
            type: 'SET_LOADING', payload: {
                tips: "Approve...",
            }
        });

        services.approve(erc20contract, web3Instance, amount, Account, maincontract.options.address).then((data) => {
            setStaking(true)
        }).catch((err) => {
            dispatch({ type: 'SET_LOADING', payload: null });
            setStaking(false)
        })
    }

    return (
        <Bg>
            <BgLayer>

                <Li>
                    <Names>Service </Names><Titles>{name} </Titles>
                </Li>
                <Li>
                    <Names>My balance</Names>
                    <Titles2>{PublicObj.ToThousands(mybalance ? mybalance : 0)} APN </Titles2>


                </Li>
                <Li>
                    <Names>Amount</Names><Inpt type="number" value={amount} onChange={handleChange} /> <Max onClick={() => tomax()}>max</Max>
                </Li>
                <LastRow>
                    <BtnClose onClick={closeBox}>Close</BtnClose>
                    <Btn onClick={() => handleStake()}>Contribute</Btn>
                </LastRow>
            </BgLayer>
        </Bg>
    );

}
